
.card::before{
  position:absolute;
  content:'';
  background-color:#1E3164;
  height:95px;
  width:35px;
  top:0.9rem;
  right:11px;
  transform:rotate(45deg);
  z-index:-6;
  
}

.card::after{
  position:absolute;
  content:attr(data-label);
  top:8px;
  padding-left:20px;
  padding:6px;
  padding-top:4px;
  padding-left:16px;
  right:-17px;
  width:5.2rem;
  height: 2rem;
  background-color:#1E3164;
  z-index:12;
  border-bottom-left-radius:20px;
  border-top-left-radius:20px;
  border-top-right-radius:10px;
  color:#fff;
} 


.logoSylvain {
  width: 20% !important; /* Taille initiale du logoSylvain pour les grands écrans */
}

.title {
  font-size: 50px !important; /* Taille initiale du title pour les grands écrans */
  font-family: 'Titillium Web', sans-serif !important;
}

.slogan {
  font-size: 20px !important; /* Taille initiale du title pour les grands écrans */
  font-family: 'Titillium Web', sans-serif !important;
}

@media (max-width: 575.98px) {
  .logoSylvain {
    width: 40% !important; /* Ajustez la taille du logoSylvain pour les très petits écrans */
  }

  .title {
    font-size: 35px !important; /* Ajustez la taille du title pour les très petits écrans */
  }
}

/* Nouvelle règle pour les écrans jusqu'à 399px */
@media (max-width: 399px) {
  .logoSylvain {
    width: 50% !important; /* Potentiellement augmentez la taille du logoSylvain pour les écrans extrêmement petits */
  }

  .title {
    font-size: 25px !important; /* Réduisez significativement la taille du title pour les écrans extrêmement petits */
  }
}

/* Petits écrans (téléphones mobiles en mode paysage, 576px et plus) */
@media (min-width: 576px) and (max-width: 767.98px) {
  .logoSylvain {
    width: 35% !important; /* Ajustez la taille du logoSylvain pour les petits écrans */
  }

  .title {
    font-size: 22px !important; /* Ajustez la taille du title pour les petits écrans */
  }
}

/* Écrans moyens (tablettes, 768px et plus) */
@media (min-width: 768px) and (max-width: 991.98px) {
  .logoSylvain {
    width: 30% !important; /* Ajustez la taille du logoSylvain pour les écrans moyens */
  }

  .title {
    font-size: 24px !important; /* Ajustez la taille du title pour les écrans moyens */
  }
}

/* Grands écrans (ordinateurs de bureau, 992px et plus) */
@media (min-width: 992px) and (max-width: 1199.98px) {
  .logoSylvain {
    width: 25% !important; /* Ajustez la taille du logoSylvain pour les grands écrans */
  }

  .title {
    font-size: 40px !important; /* Ajustez la taille du title pour les grands écrans */
  }
}

/* Très grands écrans (grands ordinateurs de bureau, 1200px et plus) */
@media (min-width: 1200px) {
  .logoSylvain {
    width: 20% !important; /* Retour à la taille initiale pour les très grands écrans */
  }

  .title {
    font-size: 50px !important; /* Retour à la taille initiale du title pour les très grands écrans */
  }
}

